// apiClient.js

import axios from "axios";

const apiClient = axios.create({
    baseURL: 'http://127.0.0.1:2309/', // Địa chỉ máy chủ
    timeout: 10000, // Thời gian chờ tối đa cho mỗi yêu cầu (ms)
    headers: {
        'Content-Type': 'application/json', // Loại dữ liệu được gửi đi
        'Accept': 'application/json', // Loại dữ liệu được chấp nhận từ máy chủ
    },
});

// Export apiClient để có thể sử dụng trực tiếp trong các module khác
export default apiClient;
